






















import { Component, Vue } from 'vue-property-decorator';
import { State, Action } from 'vuex-class';
import { IType, IListItem } from '@/types/interface';
import Banner from '../../components/Banner.vue';
import BaseSearchSelection from '../../components/BaseSearchSelection.vue';
import BaseFooter from '../../components/BaseFooter.vue';
import BaseLabelList from '../../components/BaseLabelList.vue';

@Component({
  components: {
    Banner,
    BaseSearchSelection,
    BaseFooter,
    BaseLabelList,
  },
})

export default class InterestExam extends Vue {
  @Action('getTypeMenu', { namespace: 'testKind' }) getTypeMenu!: () => void
  @State('typeMenu', { namespace: 'testKind' }) stateTypeMenu!: IType[] | [];

  listOne = [];
  // listTwo = [];

  get tempList(): IListItem[] | [] {
    return [...this.stateTypeMenu].map((d: IType) => ({
      key: d.typename,
      val: d.title,
    }));
  }

  get isMemberProcess() {
    return this.$route.query.process === 'member';
  }

  created() {
    this.getTypeMenu();
  }

  link() {
    this.$router.push('/member');
  }

  skip() {
    this.$router.push('/member/edit_subscribe');
  }
}
